.hpblock {
  @apply flex flex-col xl:flex-row pt-32 items-center;
}

.hpblock .hpblock-left {
  @apply basis-full xl:basis-1/2 p-2 min-h-full relative xl:p-0 p-8 lg:p-16;
}

.hpblock .hpblock-left .hpblock-left-dyntext {
  @apply pt-8;
}

.hpblock .hpblock-left .hpblock-left-dyntext .title-home {
  @apply flex-row lg:flex-col;
}

.hpblock .hpblock-left .hpblock-left-dyntext .title-home .title-home-dyn {
  @apply w-full pb-[85px] lg:pb-[35px];
}

.hpblock .hpblock-left .hpblock-left-dyntext .title-home .title-home-dyn .splited-text {
  @apply w-full -left-[9px];
}

.hpblock .hpblock-left .hpblock-left-desc {
  @apply paragraph-lg pb-0 lg:pb-8 text-contrast lg:pr-32 p-0;
}

.hpblock .hpblock-left .hpblock-left-btn {
  @apply pb-8;
}

.hpblock .hpblock-right {
  @apply basis-full xl:basis-1/2 p-0 h-[calc(720px-110px)] pt-8 w-full xl:w-auto hidden xl:block;
}

.hpblock .hpblock-right .hpblock-right-image-1 {
  @apply relative float-right hidden;
  animation-name: floating2;
  animation-duration: 10s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}

@keyframes floating2 {
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(0, 1rem);
  }
  100% {
    transform: translate(0, 0);
  }
}
.hpblock .hpblock-right .hpblock-right-image-1 .hpblock-right-image-elt {
  @apply relative z-10 w-1/2 lg:w-[440px];
}

.hpblock .hpblock-right .hpblock-right-image-1 .hpblock-right-image-blurry {
  @apply blur-3xl absolute z-0 left-0 w-1/2 xl:w-[440px] translate-y-8 opacity-60;
}

.hpblock .hpblock-right .hpblock-right-image-2 {
  @apply relative translate-x-0 lg:-translate-x-16 translate-y-0 z-10 rounded-lg;
  animation-name: floating;
  animation-duration: 8s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}

.hpblock .hpblock-right .hpblock-right-image-2.play {
  animation-play-state: paused;
}

.hpblock .hpblock-right .hpblock-right-image-2.play:hover .btn-play {
  @apply block opacity-50;
}

.hpblock .hpblock-right .hpblock-right-image-2.play:hover .btn-play svg.pause {
  @apply block;
}

.hpblock .hpblock-right .hpblock-right-image-2.play:hover .btn-play svg.play {
  @apply hidden;
}

.hpblock .hpblock-right .hpblock-right-image-2.play .btn-play {
  @apply hidden;
}

@keyframes floating {
  0% {
    transform: translate(-4rem, 0);
  }
  50% {
    transform: translate(-5rem, 1rem);
  }
  100% {
    transform: translate(-4rem, 0);
  }
}
.hpblock .hpblock-right .hpblock-right-image-2 .hpblock-right-image-elt {
  @apply relative z-10 rounded-lg w-1/2 lg:w-full;
}

.hpblock .hpblock-right .hpblock-right-image-2 .hpblock-right-image-blurry {
  @apply blur-3xl absolute z-0 left-0 w-1/2 lg:w-full translate-y-8 opacity-60;
}

.hpblock .hpblock-right .hpblock-right-image-2 video {
  @apply rounded-lg z-10 relative;
}